/**
 * Categories are themed groupings of FAQ question entries that a site owner can create to organize their
 * FAQ questions. For example, Shipping or Returns. Each category can contain multiple questions and is displayed
 * as a section on a site's Wix FAQ widgets.
 */
export interface Category {
  /**
   * Category ID.
   * @format GUID
   * @readonly
   */
  _id?: string | null;
  /**
   * Revision number, which increments by 1 each time the category is updated.
   * To prevent conflicting changes,
   * the current revision must be specified when updating the category.
   *
   * Ignored when creating a category.
   * @readonly
   */
  revision?: string | null;
  /**
   * Date and time the category was created.
   * @readonly
   */
  _createdDate?: Date | null;
  /**
   * Date and time the category was last updated.
   * @readonly
   */
  _updatedDate?: Date | null;
  /**
   * Category title displayed on a site's Wix FAQ widgets.
   * @maxLength 500
   * @minLength 1
   */
  title?: string | null;
  /** Order of the category within a site's Wix FAQ dashboard page. Categories with lower sort order values appear first. */
  sortOrder?: number | null;
  /**
   * Custom field data for the `category` object.
   *
   * [Extended fields](https://dev.wix.com/docs/build-apps/develop-your-app/extensions/backend-extensions/schema-plugins/about-schema-plugin-extensions) must be configured in the app dashboard before they can be accessed with API calls.
   */
  extendedFields?: ExtendedFields;
}

export interface ExtendedFields {
  /**
   * Extended field data. Each key corresponds to the namespace of the app that created the extended fields.
   * The value of each key is structured according to the schema defined when the extended fields were configured.
   *
   * You can only access fields for which you have the appropriate permissions.
   *
   * Learn more about [extended fields](https://dev.wix.com/docs/rest/articles/getting-started/extended-fields).
   */
  namespaces?: Record<string, Record<string, any>>;
}

