/**
 * Updates a category.
 *
 * Each time the category is updated, `revision` increments by 1.
 * The current `revision` must be specified when updating the category.
 * This ensures you're working with the latest category and prevents unintended overwrites.
 * Updates can be partial.
 */
export function updateCategory(payload: object): RequestOptionsFactory<any> {
  function __updateCategory({ host }: any) {
    const serializedData = transformPaths(payload, [
      {
        transformFn: transformSDKFieldMaskToRESTFieldMask,
        paths: [{ path: 'fieldMask' }],
      },
      {
        transformFn: transformSDKTimestampToRESTTimestamp,
        paths: [
          { path: 'category.createdDate' },
          { path: 'category.updatedDate' },
        ],
      },
      {
        transformFn: transformSDKFloatToRESTFloat,
        paths: [{ path: 'category.sortOrder' }],
      },
    ]);
    const metadata = {
      entityFqdn: 'wix.faq.category.v2.category',
      method: 'PATCH' as any,
      methodFqn: 'wix.faq.category.v2.CategoryService.UpdateCategory',
      packageName: PACKAGE_NAME,
      url: resolveWixFaqCategoryV2CategoryServiceUrl({
        protoPath: '/v2/categories/{category.id}',
        data: serializedData,
        host,
      }),
      data: serializedData,
      transformResponse: (payload: any) =>
        transformPaths(payload, [
          {
            transformFn: transformRESTTimestampToSDKTimestamp,
            paths: [
              { path: 'category.createdDate' },
              { path: 'category.updatedDate' },
            ],
          },
          {
            transformFn: transformRESTFloatToSDKFloat,
            paths: [{ path: 'category.sortOrder' }],
          },
        ]),
    };

    return metadata;
  }

  return __updateCategory;
}

/**
 * Deletes a category.
 *
 * Deleting a category permanently removes it from the site's FAQ page.
 * Questions that belong to the deleted category are also deleted.
 */
export function deleteCategory(payload: object): RequestOptionsFactory<any> {
  function __deleteCategory({ host }: any) {
    const metadata = {
      entityFqdn: 'wix.faq.category.v2.category',
      method: 'DELETE' as any,
      methodFqn: 'wix.faq.category.v2.CategoryService.DeleteCategory',
      packageName: PACKAGE_NAME,
      url: resolveWixFaqCategoryV2CategoryServiceUrl({
        protoPath: '/v2/categories/{categoryId}',
        data: payload,
        host,
      }),
      params: toURLSearchParams(payload),
    };

    return metadata;
  }

  return __deleteCategory;
}

